<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page, index) of pages" :key="index">
        <router-link tag="div" :to="'detail_'+item.id" class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl">
          </div>
          <p class="icon-title">{{item.desc}}</p>
        </router-link>
      </swiper-slide>
    </swiper>
    <div class="swiper-pagination" slot="pagination"></div>
  </div>
</template>

<script>
export default {
  name: 'VIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false,
        pagination: '.swiper-pagination'
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl'
@import '~@/assets/styles/mixins.styl'

  .icons >>> .swiper-container
    height 0
    width 100%
    padding-bottom 50%
  .icons >>> .swiper-pagination
    position initial
    padding-bottom 0.1rem
  .icons >>> .swiper-pagination-bullet
    margin 0 0.1rem
  // .icons >>> .swiper-pagination-bullet-active
  //   background-color #ffffff

  .icons
    padding-top 0.2rem
    overflow hidden
    position relative
    background-color $whiteColor
    .icon
      position relative
      width 25%
      height 0
      float left
      padding-bottom 25%
      .icon-img
        position absolute
        left 0
        right 0
        top 0
        bottom 0.44rem
        overflow hidden
        box-sizing border-box
        padding 0.14rem
        .icon-img-content
          display block
          height 100%
          margin 0 auto
          height 100%
      .icon-title
        position absolute
        left 0
        right 0
        bottom 0
        height 0.44rem
        line-height 0.44rem
        text-align center
        color $darkTextColor
        ellipsis()
</style>



