<template>
  <div class="weekend">
    <div class="title">
      <inline-icon :type="0" :size="2"></inline-icon>
      周末去哪
    </div>
    <ul>
      <router-link
        tag="li"
        class="item"
        v-for="item of list"
        :key="item.id"
        :to="'/detail_' + item.id"
      >
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" :alt="item.title" />
        </div>
        <div class="item-info-wrapper">
          <div class="item-info-title">{{item.title}}</div>
          <div class="item-info-desc">{{item.desc}}</div>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
import InlineIcon from '@/components/inline-icon/InlineIcon.vue'
export default {
  name: 'VWeekend',
  props: {
    list: Array
  },
  components: {
    InlineIcon
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/mixins.styl'
@import '~assets/styles/varibles.styl'

  .weekend
    margin-top 0.1rem
    .title
      line-height 0.8rem
      height 0.8rem
      text-indent 0.2rem
    .item
      overflow hidden
      margin-bottom: 0.1rem;
      background: #ffffff;
      .item-img-wrapper
        width 100%
        height 0
        padding-bottom 33.43%
        overflow hidden
        .item-img
          width 100%
      .item-info-wrapper
        padding 0.2rem
        .item-info-title
          padding-right 1.4rem
          height 0.50rem
          line-height 0.50rem
          font-size 0.30rem
          color #212121
          ellipsis()
        .item-info-desc
          padding-right 1.4rem
          height 0.42rem
          line-height 0.42rem
          font-size 0.24rem
          color #616161
          ellipsis()
</style>



