<template>
  <span class="star-wrapper">
    <span class="iconfont star">&#xe625;&#xe625;&#xe625;&#xe625;&#xe625;</span>
    <span class="iconfont star-solid" :style="{width: this.score+'%'}" >&#xe753;&#xe753;&#xe753;&#xe753;&#xe753;</span>
  </span>
</template>

<script>
export default {
  name: 'VStar',
  props: {
    score: {
      type: Number,
      default: 70
    }
  }
}
</script>

<style lang="stylus" scoped>
.star-wrapper
  display inline-block
  position relative
  letter-spacing .02rem
  .iconfont
    position absolute
    overflow hidden
    top 0.04rem
    left 0
    height 0.28rem
    font-size 0.28rem
    &.star-solid
      color #ffb436
</style>



