<template>
    <div class="home-page">
      <v-header class="home-header"></v-header>
      <div class="home-main">
        <v-swiper :list="swiperList"></v-swiper>
        <v-icons :list="iconList"></v-icons>
        <v-recommend :list="recommendList"></v-recommend>
        <v-weekend :list="weekendList"></v-weekend>
      </div>
      <v-footer class="home-footer"></v-footer>
    </div>
</template>

<script>
import VHeader from '@/components/header/Header.vue'

import VSwiper from '@/components/swiper/Swiper.vue'
import VIcons from '@/components/icons/Icons.vue'
import VRecommend from '@/components/recommend/Recommend.vue'
import VWeekend from '@/components/weekend/Weekend.vue'

import VFooter from '@/components/footer/Footer.vue'
import axios from 'axios'
import { mapState } from 'vuex'
export default {
  name: 'Home',
  components: {
    VHeader,
    VSwiper,
    VIcons,
    VRecommend,
    VWeekend,
    VFooter
  },
  data () {
    return {
      swiperList: [],
      iconList: [],
      recommendList: [],
      weekendList: [],
      lastCity: ''
    }
  },
  computed: {
    ...mapState(['city'])
  },
  methods: {
    getHomeInfo () {
      axios.get(`/api/index.json?city=${this.city}`)
        .then(this.getHomeInfoSucc)
    },
    getHomeInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        this.swiperList = res.data.swiperList
        this.iconList = res.data.iconList
        this.recommendList = res.data.recommendList
        this.weekendList = res.data.weekendList
      }
    }
  },
  mounted () {
    this.lastCity = this.city
    this.getHomeInfo()
  },
  activated () {
    if (this.lastCity !== this.city) {
      this.lastCity = this.city
      this.getHomeInfo()
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/varibles.styl'

.home-page
  background-color $home-page-bgColor
</style>



