<template>
  <div class="recommend">
    <div class="title">
      <inline-icon :type="1" :size="3"></inline-icon>
      热销推荐
    </div>
    <ul class="like-list">
      <router-link
        tag="li"
        class="like-item border-bottom"
        v-for="item of list"
        :key="item.id"
        :to="'/detail_' + item.id"
      >
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" :alt="item.title" />
        </div>
        <div class="item-info">
          <div class="item-info-title">{{item.title}}</div>
          <div class="item-info-comment">
            <v-star :score="item.score" class="comment-star"></v-star>
            <span class="comment-num">{{item.comment}}条评论</span>
          </div>
          <div class="item-info-price">
            <span class="price">￥<em>{{item.price}}</em></span>起
            <span class="adress">{{item.adress}}</span>
          </div>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
import InlineIcon from '@/components/inline-icon/InlineIcon.vue'
import VStar from '@/components/star/Star.vue'

export default {
  name: 'VRecommend',
  props: {
    list: Array
  },
  components: {
    InlineIcon,
    VStar
  }
}
</script>

<style lang="stylus" scoped>
@import '~assets/styles/mixins.styl'
@import '~assets/styles/varibles.styl'

.recommend
  background-color $whiteColor
  margin-top 0.1rem
  .title
    line-height 0.8rem
    height 0.8rem
    text-indent 0.2rem
  .like-list
    margin-left 0.24rem
    .like-item
      padding 0.2rem 0
      display flex
      overflow hidden
      .item-img-wrapper
        width 2rem
        height 2rem
        .item-img
          width 100%
      .item-info
        flex 1
        padding-left 0.22rem
        min-width 0
        .item-info-title
          margin-top 0.26rem
          height 0.44rem
          line-height 0.44rem
          font-size 0.32rem
          color $textColor
          ellipsis()
        .item-info-comment
          margin-top 0.14rem
          height 0.34rem
          line-height 0.34rem
          ellipsis()
          .comment-star
            width 1.5rem
            height 0.28rem
            line-height 0.28rem
          .comment-num
            color $textColor-light
            font-size 0.24rem
            line-height 0.34rem
            vertical-align text-bottom
        .item-info-price
            position relative
            margin-top 0.22rem
            color $textColor-light
            font-size .24rem
            line-height .4rem
            .price
              color #ff8300
              em
                font-size 0.4rem
            .adress
              position absolute
              right 0.2rem
</style>



