<template>
  <span :class="cls" class="inline-icon"></span>
</template>

<script>
export default {
  name: 'InlineIcon',
  props: {
    type: {
      type: Number,
      default: 1
    },
    size: {
      type: Number,
      default: 2
    }
  },
  computed: {
    cls () {
      const typeMap = ['heart', 'hot']
      return `icon-${this.size} ${typeMap[this.type]}`
    }
  }
}
</script>

<style lang="stylus" scoped>
.inline-icon
  display inline-block
  background-repeat no-repeat

.icon-1
  width 12px
  height 12px
  background-size 12px 12px
  &.heart
    background-image url(./heart.png)
  &.hot
    background-image url(./hot.png)

.icon-2
  width 14px
  height 14px
  background-size 14px 14px
  &.heart
    background-image url(./heart.png)
  &.hot
    background-image url(./hot.png)

.icon-3
  width 16px
  height 16px
  background-size 16px 16px
  &.heart
    background-image url(./heart.png)
  &.hot
    background-image url(./hot.png)

</style>



